@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	html {
		overflow: hidden;
		height: 100%;
	}
	body {
		overflow: auto;
		height: 100%;
	}
}

a {
	@include transition(color, var(--transitionTime), ease);
	@include transition(background-color, var(--transitionTime), ease);
	outline: 0 !important;
}

.body-bg {
	display: none;
}

.al-header {
	display: block;
	height: 49px;
	margin: 0;
	background-repeat: repeat-x;
	position: relative;
	z-index: 905;
	color: #444444;
}

.al-main {
	//margin-left: var(--sidebar-width);
	display: flex;
	flex: 1;
	//padding: var(--top-height) 0 var(--footer-height) 0;
	min-height: 100%;
	//width: 100%;
	//width: calc(100% - var(--sidebar-width));
	position: relative;
	overflow: auto;
}

.al-footer {
	height: var(--footer-height);
	padding: 0px 18px 0 var(--sidebar-width);
	width: 100%;
	position: absolute;
	display: block;
	bottom: 0;
	font-size: 13px;
	color: var(--default-text);
	@include transition(padding-left, 0.5s, ease);
}

.al-footer-main {
	float: left;
	margin-left: 15px;
}

.al-copy {
	float: left;
}

.al-footer-right {
	float: right;
	margin-right: 12px;

	i {
		margin: 0 4px;
		color: var(--danger);
		font-size: 12px;
	}

	a {
		margin-left: 4px;
		color: var(--default-text);

		&:hover {
			color: var(--danger);
		}
	}
}

.al-share {
	margin: -6px 0 0 12px;
	padding: 0;
	list-style: none;
	float: left;

	li {
		list-style: none;
		float: left;
		margin-left: 16px;

		i {
			cursor: pointer;
			transition: all 0.1s ease;
			color: white;
			padding: 6px;
			box-sizing: content-box;
			font-size: 16px;

			&:hover {
				transform: scale(1.2);
			}
		}

		i.fa-facebook-square {
			color: var(--facebook-color);
		}

		i.fa-twitter-square {
			color: var(--twitter-color);
		}

		i.fa-google-plus-square {
			color: var(--google-color);
		}
	}
}

.al-content {
	width: 100%;
	//margin-top: 10px;
	padding: 20px;
	position: relative;
	overflow-x: hidden;
}

@media screen and (max-width: $resXS) {
	.al-content {
		padding: 8px 20px;
	}
}

.vis-hidden {
	visibility: hidden;
	position: absolute;
	top: -9999px;
	left: -9999px;
}

.icon-up, .icon-down {
	width: 5px;
	height: 13px;
	display: block;
}

