.disable-text-selection {
	-webkit-touch-callout: none;
	user-select: none;
}

.align-right {
	text-align: right
}

.amcharts-chart-div > a {
	font-size: 6px !important;
}

.content-panel {
	padding-left: 22px;
	padding-top: 26px;
}

@media (max-width: 590px) {
	.al-footer-right {
		float: none;
		margin-bottom: 19px;
		margin-right: 0;
	}
	.al-footer {
		height: 76px;
		text-align: center;
	}
	.al-main {
		padding-bottom: 76px;
	}
	.al-footer-main {
		float: none;
		display: inline-block;
	}
}

.full-invisible {
	visibility: hidden !important;

	* {
		visibility: hidden !important;
	}
}

.irs-grid-text {
	color: var(--default-text);
}

.col-1p5 {
	-ms-flex: 0 0 12.5%;
	flex: 0 0 12.5%;
	max-width: 12.5%;
}
.col-10p5 {
	-ms-flex: 0 0 87.5%;
	flex: 0 0 87.5%;
	max-width: 87.5%;
}

.col-1p5, .col-10p5 {
	position: relative;
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
}
